@import "../../../global.scss";

.portfolio-list-a {
  text-decoration: none;

  .list {
    font-size: 14px;
    margin: 0px 25px;
    padding: 7px 11px;
    border-radius: 50px;
    color: $textColor;
    background-color: $primaryColorFaded;
    height: auto;
    cursor: pointer;
    margin-bottom: 0px;
    transition: all linear 0.2s;
    overflow: visible;

    @include mobile {
      margin: 0px 10px;
    }

    &.active {
      background-color: $accentColor;
      color: black;
    }
  }
}
